import React from 'react'

export default function button({currentColor,ownColor,onClick,style}) {
    const isActive = currentColor === ownColor;
    return (
        <button style={{width:"36px",height:"12px",backgroundColor: isActive? "#989797" : "#C4C4C4",outline:"none",border:"none",...style}} onClick={onClick}></button>
    )
}
