import React from 'react'
import "./item.css";

export default function Item({value,style,key}) {
    return (
        <div style={{width:"270px",height:"340px",lineHeight:"340px",textAlign:"center",backgroundColor:"#C4C4C4",...style}} className={"item"} key={key}> 
            {value}
        </div>
    )
}
