import React,{useState} from 'react'
import Button from "../common/Button"; 

export default function Header() {
    const [color,ChangeColor] = useState("#D2D2D2");
    return (
        <div style={{display:"flex",margin:"0 auto",maxWidth:"1170px",width:"100%",minWidth:"1005px"}}>
            <div style={{width:"470px",paddingTop:"118px"}}>
                <h2 style={{fontWeight:"700",fontSize:"36px",marginBottom:"15px"}}>Вся одежда<br/> изготовлена в Италии</h2>
                <p style={{width:"370px",lineHeight:"21px",marginBottom:"19px"}}>Весь товар подлежит хорошему и достойному качесву! Пополните свой гардероб стильной и модной одеждай.</p>
                <button style={{width:"270px",height:"50px",textAlign:"center",lineHeight:"50px",fontSize:"20px",outline:"none",border:"none",backgroundColor:" #747373",fontWeight:"700"}}>КАТАЛОГ</button>
            </div>
            <div style={{display:"flex",flexGrow:"1",justifyContent:"flex-end"}}>
                <div style={{width:"470px",height:"468px",backgroundColor:`${color}`}}>
                    <Button ownColor={"#D2D2D2"} currentColor={color} onClick={()=>{ChangeColor("#D2D2D2")}} style={{margin:"415px 25px 0 42px"}}/>
                    <Button ownColor={"green"} currentColor={color} onClick={()=>{ChangeColor("green")}} style={{marginRight:"25px"}}/>
                    <Button ownColor={"red"} currentColor={color} onClick={()=>{ChangeColor("red")}}/>
                </div>
            </div>
        </div>
    )
}
