import React from 'react'

export default function NavBar() {
    const links = ["Головные уборы","Пальто","Кастюмы","Брюки","Обувь","Аксессуары"];
    return (
        <div style={{width:"100%",backgroundColor:"#C4C4C4",height:"85px",minWidth:"1005px"}}>
            <div style={{display:"flex",maxWidth:"1170px",width:"100%",margin:"0 auto",fontSize:"14px",height:"85px"}}>
                <div style={{display:"flex"}}> 
                    <div style={{width:"47px",height:"40px",backgroundColor:"#959494",textAlign:"center",lineHeight:"40px",marginTop:"23px",fontSize:"13px"}}><b>Logo</b></div>
                    <div>
                        <h6 style={{margin: "27px 0 0 10px",fontSize:"16px",fontWeight:"700"}}>MARTEEZ</h6>
                        <p style={{marginLeft:"10px",whiteSpace:"nowrap"}}>Сайт магазина одежды</p>
                    </div>
                </div>
                <ul style={{display:"flex",listStyleType:"none",margin: "37px 0 0 76px",padding:"0"}}>
                    {links.map((i)=><li style={{marginRight:"36px",whiteSpace:"nowrap"}}>{i}</li>)}
                </ul>
                <div style={{display:"flex",justifyContent:"flex-end",flexGrow:"1",fontSize:"14px",lineHeight:"95px",marginRight:"2px"}}><b>Корзина</b></div>
            </div>
        </div>
    )
}
